import React, { useState } from 'react'
import PropTypes from 'prop-types'

const style = {
    form: {
        marginTop: '30px',
        marginBottom: '1rem',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
    }
}

function AddTodo({ onCreate }) {
    const [value, setValue] = useState('')

    function submitHandler(event) {
        event.preventDefault()

        if (value.trim()) {
            onCreate(value)
            setValue('')
        }
    }

    return (
        <form style={style.form} onSubmit={submitHandler}>
		<input className='input-add' value={value} placeholder="Enter text" onChange={event=>setValue(event.target.value)}/>
		<button className='info-button infb2-s infb2-p' type="submit">Add todo</button>
		</form>
    )
}

AddTodo.propTypes = {
    onCreate: PropTypes.func.isRequired
}

export default AddTodo